import type { FC, ReactNode, MouseEvent } from 'react'
import { useCallback, useState, useMemo } from 'react'

import Popover from '@UI/Popover'
import { cn } from '@utils/ui'

import downArrowIcon from '@/assets/black_down_arrow.svg'

import type { PopoverProps } from '../Popover' // AZ-NOTE: do not replace relative import of types

import {
  LABEL_CLASS_NAME,
  POPOVER_CLASS_NAME
} from './styles'

const MIN_POPOVER_WIDTH = 184

export interface HiddenItemsLabelProps {
  text: string
  icon?: ReactNode
  content?: ReactNode
  popoverProps?: Omit<PopoverProps, 'anchorEl' | 'onClose' | 'open'>
  className?: string
}

const HiddenItemsLabel: FC<HiddenItemsLabelProps> = ({
  text,
  content = null,
  className = '',
  popoverProps = {},
  icon = null
}) => {
  const [anchorEl, setAnchorEl] = useState<HTMLParagraphElement | null>(null)

  const mouseEnterHandler = useCallback(({ currentTarget }: MouseEvent<HTMLParagraphElement>) => {
    setAnchorEl(currentTarget)
  }, [])

  const mouseLeaveHandler = useCallback(() => {
    setAnchorEl(null)
  }, [])

  const isContentExist = content != null

  const iconComp = useMemo(() =>
    icon ?? <img src={downArrowIcon} alt='down_arrow' />
  , [icon])

  return (
    <>
      {text != null && (
        <p
          className={cn(LABEL_CLASS_NAME, className)}
          onMouseOver={mouseEnterHandler}
          onMouseLeave={mouseLeaveHandler}
        >
          {text}
          {isContentExist && iconComp}
        </p>
      )}

      {isContentExist && (
        <Popover
          open={anchorEl != null}
          anchorEl={anchorEl}
          minWidth={MIN_POPOVER_WIDTH}
          className={cn(POPOVER_CLASS_NAME, popoverProps?.className ?? '')}
          {...popoverProps}
        >
          {content}
        </Popover>
      )}
    </>
  )
}

export default HiddenItemsLabel
